    import HMmessages from "../../../components/HM-messages/HMmessages";
	export default {
		name:"MyTextArea",
		props:{
			editText:String
		},
	    data() {
	        return {
                content:""
			}
	    },
        components:{
		    HMmessages
        },
	    methods: {
            textareaBInput(e) {
                this.content = e.detail.value
                this.$emit('getContent',this.content)
            },
            warning(msg) {
                this.HMmessages.show(msg, {iconColor: '#ffffff', fontColor: '#ffffff', background: "#ffd655"})
            }
	    }
	}
